ul {
    list-style-type: none;
	margin: 0;
    padding: 0;
}
.img {
	background-size: cover;
    background-position: center;
	margin: 2.5%;
	width: 95%;
	height: 50vh;
	display: table;
	position: relative;
    border-radius: 15px;
    box-shadow: 1px 1px 20px -5px;  
	transition-duration: 0.2s;
    transition-timing-function: linear;
}
.showinbasket {
    width: 47.5%;
    height: 25vh;
	transition-duration: 0.2s;
    transition-timing-function: linear;
}
.img_title {
    font-size: calc(16px + 2vmin);
    color: black;
    font-weight: bold;
	display: flex;
    align-items: center;
    justify-content: space-between;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0;
    padding: 15px;
    position: absolute;
    border-radius: 15px;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-color: rgba(255, 255, 255, 0.5);
    box-shadow: 0px 5px 10px -5px;
	transition-duration: 0.2s;
    transition-timing-function: linear;
}
.showinbasket .img_title {
    width: 100%;
    height: 25vh;
    position: absolute;
    margin-left: 100%;
    box-shadow: none;
    background: none;
    -webkit-backdrop-filter: none;
    backdrop-filter: none;
    flex-wrap: wrap;
	transition-duration: 0.2s;
    transition-timing-function: linear;
}
.countbox {
	display: flex;
	align-items: center;
}
.countbox_buttonminus {
    font-size: calc(16px + 2vmin);
    color: black;
    font-weight: bold;
    margin: 0px;
    padding: 0px;
	width: 47px;
    height: 47px;
    border-radius: 100%;
    border: 0px solid yellow;
    background: yellow;
    box-shadow: 1px 1px 20px -5px;
}
.countbox_input {
	border: none;
    background: none;
    width: 60px;
    font-size: calc(16px + 2vmin);
    color: black;
    font-weight: bold;
	text-align: center;
	}
.countbox_input[value="0"] , .countbox_input[value=""] , .countbox_buttonminus:disabled {
    opacity: 0;
	}
.countbox_buttonplus {
    font-size: calc(16px + 2vmin);
    color: black;
    font-weight: bold;
	margin: -10px;
    margin-top: -45px;
    padding: 0px;
    width: 90px;
    height: 90px;
    border-radius: 100%;
    border: 0px solid yellow;
    background: yellow;
    box-shadow: 1px 1px 20px -5px;
	transition-duration: 0.2s;
    transition-timing-function: linear;
}
.showinbasket .countbox_buttonplus {
	width: 47px;
    height: 47px;
    margin: 0px;
    padding: 0px;
	transition-duration: 0.2s;
    transition-timing-function: linear;
	}
button:hover {
    box-shadow: 1px 1px 10px 0px;
    background: #ffcf00;
}
.countbox_buttonplus:disabled {
    background: #b5b5b5;
    box-shadow: 1px 1px 10px -5px;
}
button:focus, input:focus {
    outline: none;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
	margin: 0;
}
input[type='number'] {
    -moz-appearance: textfield;
}



.footer {
  position: sticky;
  bottom: 0px;
  width: 100%;
  margin: 0px;
  height: auto;
  z-index: 50;
    background: #fff;
	display: -webkit-flex;
display: flex;
}
.footer_button {
    font-size: calc(16px + 2vmin);
    color: black;
    font-weight: bold;
	text-align: center;
	margin: 10px 2.5%;
    padding: 5px;
	flex: 1 1 60px;
    border: 0px solid yellow;
    background: yellow;
}
.hide, .hideinbasket {
	display: none;
	transition-duration: 0.2s;
    transition-timing-function: linear;
}




* {box-sizing: border-box;}
.top {
	width: 95%;
	position: sticky;
	top: 10px;
	display: flex;
	align-items: center;
	justify-content: flex-end;
	z-index: 50;
}
.search {
  position: relative;
  width: 300px;
  margin: 10px;
  height: 47px;
  z-index: 50;
}
.search input {
    height: 47px;
    width: 0;
    padding: 0 32px 0 15px;
    border: none;
    outline: none;
    background: #ffff0078;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    transition: .4s cubic-bezier(0, 0.8, 0, 1);
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    border-radius: 50px;
    box-shadow: 1px 1px 20px -5px;
	opacity: 0;
	font-size: calc(16px + 2vmin);
    color: black;
    font-weight: bold;
}
.search input:focus, .search input.notempty {
	width: 300px;
	z-index: 1;
	opacity: 1;
}
.search button {
	background: yellow;
    border: none;
    height: 47px;
    width: 47px;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
    border-radius: 50px;
    box-shadow: 1px 1px 20px -5px;
}
.search button.notempty {
z-index: 2;
}
.search button:before{
    content: "\f002";
    font-family: FontAwesome;
	font-size: calc(16px + 2vmin);
    color: black;
}
.search button.notempty:before{
    content: "\f00d";
    font-family: FontAwesome;
	font-size: calc(16px + 2vmin);
    color: red;
}
.basket {
	background: yellow;
    border: none;
    height: 47px;
    width: 47px;
    top: 0;
    right: 0;
    cursor: pointer;
    border-radius: 50px;
    box-shadow: 1px 1px 20px -5px;
}
.basket:before{
    content: "\f07a";
    font-family: FontAwesome;
	font-size: calc(16px + 2vmin);
    color: black;
}
.basket.back:before{
    content: "\f053  ";
    font-family: FontAwesome;
	font-size: calc(16px + 2vmin);
    color: black;
}